<template>
    <main>
        <div class="Orders">
            <section class="list" v-for="(item,index) in roomsOrderData" :key="index">
                <div class="list-top clearfix">
                    <div class="list-top-l fl">订单总额：
                        <span class="red">￥{{item.realPrice|money}}</span>
                    </div>
                    <div class="list-top-r fr">
                        <span class="red">{{item.payStatus|PaymentStatus}}</span>
                        <span class="red">{{item.orderStatus|OrderStatus}}</span>
                    </div>
                </div>
                <div class="list-bottom">
                    <div class="list-bottom_above">
                        <span>房间预订</span>
                        <span>{{item.customerName}}</span>
                        <span>{{item.customerPhone}}</span>
                    </div>
                    <div class="list-bottom_middle clearfix">
                        <div class="fl">
                            <span>{{item.checkStandard |RoomType}}</span>
                            <span>{{item.number}}间</span>
                            <span class="red">({{item.payType|PaymentMethod}})</span>
                        </div>
                    </div>
                    <div class="list-bottom_base">
                        入住时间：{{item.roomTime}}
                        <template v-if="item.checkStandard!=2">
                            住{{item.tiannum}}晚
                        </template>
                    </div>
                    <p class="remain">支付剩余时间：
                        <times :endTime="item.endTime" :flag='timeshow'></times>
                    </p>
                </div>
                <div class="orderNumber clearfix">
                    <span class="fl">订单号：{{item.orderNum}}</span>
                    <div class="fr">
                        <a href="javascript:;" class="weui-btn weui-btn_mini weui-btn_warn" v-if="item.payStatus==0">支付</a>
                        <a href="javascript:;" class="weui-btn weui-btn_mini weui-btn_primary" v-if="item.orderStatus==0">取消</a>
                    </div>
                </div>
            </section>
        </div>
        <LOGO></LOGO>
    </main>
</template>

<script type='text/ecmascript-6'>
import LOGO from "@/components/Logo";
import { common, Personal } from "@/assets/js/request.js";
import times from "@/components/times";
export default {
  data() {
    return {
      hotelId: this.$store.state.hotelId,
      roomsOrderData: [],
      page: 1,
      pages: 1,
      flag: true,
      timeshow: true
    };
  },
  components: {
    LOGO,
    times
  },
  methods: {
    getOrderfn() {
      if (this.flag) {
        this.flag = false;
        Personal.roomsOrder({
          Suffix: this.hotelId + "/roomOrder",
          params: {
            hotelId: this.hotelId,
            page: this.page
          },
          fn: data => {
            this.roomsOrderData = this.roomsOrderData.concat(data.records);
            this.flag = true;
            this.pages = data.pages;
            this.roomsOrderData.forEach(function(item) {
              item.tiannum = Math.floor(
                (Date.parse(item.roomOutTime) - Date.parse(item.roomInTime)) /
                  (24 * 60 * 60 * 1000)
              );
              if (item.checkStandard == 2) {
                item.roomTime = item.roomInTime.slice(0, 16);
              } else {
                item.roomTime = item.roomInTime.slice(0, 10);
              }
              item.endTime =
                Date.parse(new Date(item.createTime)) + 30 * 60 * 1000;
            }, this);
          }
        });
      }
    },
    cancelOrderfn(id) {
      common.cancelOrder({
        Suffix: this.hotelId + "/cancel/" + id,
        fn: data => {
          this.Toast("取消成功");
          var nowpage = this.page;
          this.roomsOrderData = [];
          for (var i = 0; i < nowpage; i++) {
            this.page = i + 1;
            this.getOrderfn();
          }
        }
      });
    }
  },
  mounted() {
    var _this = this;
    _this.getOrderfn();
    //获取滚动条当前的位置
    function getScrollTop() {
      var scrollTop = 0;
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop;
      } else if (document.body) {
        scrollTop = document.body.scrollTop;
      }
      return scrollTop;
    }

    //获取当前可是范围的高度
    function getClientHeight() {
      var clientHeight = 0;
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      } else {
        clientHeight = Math.max(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      }
      return clientHeight;
    }

    //获取文档完整的高度
    function getScrollHeight() {
      return Math.max(
        document.body.scrollHeight,
        document.documentElement.scrollHeight
      );
    }
    window.onscroll = function() {
      if (getScrollTop() + getClientHeight() > getScrollHeight() - 50) {
        _this.page++;
        if (_this.page > _this.pages) {
          _this.Toast("已加载所有数据");
          return false;
        }
        _this.getOrderfn();
      }
    };
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
main {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  padding-bottom: 1.3rem;
}

.Orders {
  flex: 1; // background: #f4f4f4;
}
</style>



